import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import api from "../../services/api";

const ConfirmPage = (props) => {
  const { token } = props.match.params;
  const history = useHistory();
  const [confirmationStatus, setConfirmationStatus] = useState(null);

  useEffect(() => {
    const fetchTasks = async () => {
      try {
        const response = await api.get(`/confirm/${token}`);

        // Lgica de manipulao da resposta
        if (response.status === 200) {
          // A conta foi confirmada com sucesso
          setConfirmationStatus('success');
          console.log("Conta confirmada com sucesso");
        } else {
          // A confirmao falhou, lidar com isso adequadamente
          setConfirmationStatus('error');
          console.error("Falha na confirmao da conta");
        }
      } catch (error) {
        // Lidar com erros de rede ou solicitao
        setConfirmationStatus('error');
        console.error("Erro na solicitao de confirmao: ", error);
      }
    };

    // Chame a funo fetchTasks
    fetchTasks();
  }, [token]);

  const handleButtonClick = () => {
    // Redirecionar o usurio para a pgina inicial aps a confirmao
    history.push('/');
  };

  return (
    <div>
      <h2>{confirmationStatus === 'success' ? 'Conta Confirmada!' : 'Erro na Confirmao'}</h2>
      {confirmationStatus === 'success' ? (
        <p>Sua conta foi confirmada com sucesso. Agora voc pode fazer login e aproveitar nossos servios.</p>
      ) : (
        <p>Houve um erro ao confirmar sua conta. Por favor, tente novamente mais tarde.</p>
      )}
      <button onClick={handleButtonClick}>Ir para a Pgina Inicial</button>
    </div>
  );
};

export default ConfirmPage;
