import React, { useState, useEffect, useRef, useContext } from 'react';
import { Calendar, momentLocalizer } from 'react-big-calendar';
import moment from 'moment';
import 'moment/locale/pt-br';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import './MyCalendar.css';
import { TextField } from '@material-ui/core';
import api from "../../services/api";
import axios from "axios"; 

moment.locale('pt-br');
const localizer = momentLocalizer(moment);

const MyCalendar = ({ onEventCompleted }) => {
  const [events, setEvents] = useState([]);
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [eventTitle, setEventTitle] = useState('');
  const [eventDescription, setEventDescription] = useState('');
  const [selectedDate, setSelectedDate] = useState('');

  useEffect(() => {
    const fetchEvents = async () => {
      try {
        const response = await api.get(`${process.env.REACT_APP_BACKEND_URL}/eventos`);
        setEvents(response.data);
      } catch (error) {
        console.error('Erro ao carregar eventos:', error);
      }
    };

    fetchEvents();
  }, []);

  const handleSelectSlot = ({ start }) => {
    setSelectedDate(moment(start).format('YYYY-MM-DDTHH:mm'));
    setModalIsOpen(true);
  };

  const handleAddEvent = async () => {
    const newEvent = {
      title: eventTitle,
      description: eventDescription,
      start: new Date(selectedDate),
      end: new Date(selectedDate),
    };

    try {
      const response = await api.post(`${process.env.REACT_APP_BACKEND_URL}/eventos`, newEvent);
      setEvents([...events, response.data]);
      closeModal();
    } catch (error) {
      console.error('Erro ao adicionar evento:', error);
    }
  };

  const closeModal = () => {
    setModalIsOpen(false);
    setEventTitle('');
    setEventDescription('');
    setSelectedDate('');
  };

  const handleCompleteEvent = async (eventId) => {
    try {
      await api.put(`${process.env.REACT_APP_BACKEND_URL}/eventos/${eventId}/concluido`);
      // Atualizar o estado dos eventos marcados como concludos
      const updatedEvents = events.map((event) => {
        if (event.id === eventId) {
          return { ...event, concluido: true };
        }
        return event;
      });
      setEvents(updatedEvents);
    } catch (error) {
      console.error('Erro ao marcar evento como concludo:', error);
    }
  };

  const messages = {
    today: 'Hoje',
    previous: 'Anterior',
    next: 'Prximo',
    month: 'Ms',
    week: 'Semana',
    day: 'Dia',
    agenda: 'Agenda',
  };

  return (
    <div className="calendar-container">
      <Calendar
        localizer={localizer}
        events={events}
        startAccessor="start"
        endAccessor="end"
        selectable
        onSelectSlot={handleSelectSlot}
        messages={messages}
        className="react-big-calendar"
      />
      {modalIsOpen && (
        <div className="modal">
          <h2>Agendar Evento</h2>
          <TextField
            className="date-time-picker"
            value={selectedDate}
            onChange={(e) => setSelectedDate(e.target.value)}
            type="datetime-local"
            fullWidth
            margin="normal"
            variant="outlined"
            required
            InputProps={{ style: { border: 'none' } }}
          />
          <input
            type="text"
            placeholder="Ttulo do Evento"
            value={eventTitle}
            onChange={(e) => setEventTitle(e.target.value)}
            className="input-field"
          />
          <textarea
            placeholder="Descrio do Evento"
            value={eventDescription}
            onChange={(e) => setEventDescription(e.target.value)}
            className="text-area"
          />
          <button onClick={handleAddEvent}>Agendar</button>
          <button onClick={closeModal}>Cancelar</button>
          <button onClick={() => handleCompleteEvent(/* ID do evento */)}>
            Concludo
          </button>
        </div>
      )}
    </div>
  );
};

export default MyCalendar;
